import firebase from "firebase/app";

export default {
    actions: {
        async fetchCategories({commit, dispatch}) {
            try {
                const uid = await dispatch('getUid');
                const receivedCategories = await firebase.database().ref(`/users/${uid}/categories`).once('value');
                const categories = receivedCategories.val() || {};

                /*const cats = [];
                Object.keys(categories).forEach(key => {
                    cats.push({
                        title: categories[key].title,
                        limit: categories[key].limit,
                        id: key,
                    })
                });
                return cats;*/

                return Object.keys(categories).map(key => ({...categories[key], id: key}));
            } catch (e) {
                commit('setError', e);
                throw e;
            }
        },
        async fetchCategoryById({commit, dispatch}, id) {
            try {
                const uid = await dispatch('getUid');
                const receivedCategory = await firebase
                    .database()
                    .ref(`/users/${uid}/categories`)
                    .child(id)
                    .once('value');
                const category = receivedCategory.val() || {};

                return {...category, id};
            } catch (e) {
                commit('setError', e);
                throw e;
            }
        },
        async updateCategory({commit, dispatch}, {id, title, limit}) {
            try {
                const uid = await dispatch('getUid');
                await firebase.database().ref(`/users/${uid}/categories`).child(id).update({title, limit});
            } catch (e) {
                commit('setError', e);
                throw e;
            }
        },
        async createCategory({commit, dispatch}, {title, limit}) {
            try {
                const uid = await dispatch('getUid');
                const category = await firebase.database().ref(`/users/${uid}/categories`).push({title, limit});
                return {title, limit, id: category.key};
            } catch (e) {
                commit('setError', e);
                throw e;
            }
        }
    }
}